import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.nilesh.kotlin_portfolio.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerVariant("-circle", org.nilesh.kotlin_portfolio.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", org.nilesh.kotlin_portfolio.UncoloredButtonVariant
        )
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.HeadlineTextStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.SubheadlineTextStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.layouts.MarkdownStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.layouts.PageContentStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.sections.FooterStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.sections.NavHeaderStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.AboutStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.BackgroundImageStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.ButtonStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.HeroSectionStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.HelloImStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.UserNameStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.UsersMessageStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.ProjectStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.SectionTitleStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.SectionDescriptionStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.WorkExperienceItemRoleAndDurationStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.WorkExperienceItemOrgStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.styles.ExperienceStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.widgets.ArrowUpStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.widgets.BackToTopButtonStyle)
        ctx.theme.registerStyle(org.nilesh.kotlin_portfolio.components.widgets.FooterIconsStyle)
        ctx.theme.registerKeyframes(org.nilesh.kotlin_portfolio.components.sections.SideMenuSlideInAnim)
        ctx.theme.registerKeyframes(org.nilesh.kotlin_portfolio.components.styles.HeroContainerKeyFrames)
        ctx.theme.registerKeyframes(org.nilesh.kotlin_portfolio.components.widgets.FadeInKeyFrames)
        org.nilesh.kotlin_portfolio.initSiteStyles(ctx)
        org.nilesh.kotlin_portfolio.initTheme(ctx)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    AppGlobals.initialize(mapOf("title" to "Nilesh Sharma"))
    renderComposable(rootElementId = "root") {
        org.nilesh.kotlin_portfolio.AppEntry {
            router.renderActivePage { renderWithDeferred { it() } }
        }
    }
}
