@Composable
fun AwardsAndAchievements() {
    Column(
        modifier = ExperienceStyle.toModifier()
            .id("awards-and-achievements")
            .maxWidth(800.px)
            .margin(topBottom = 50.px),
        verticalArrangement = Arrangement.Center,
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        SectionTitle(Constants.AWARDS_AND_ACHIEVEMENTS_TITLE)

        Column(
            modifier = Modifier
                .margin(top = 30.px)
                .padding(20.px)
                .backgroundColor(Color.lightgray)
                .borderRadius(10.px)
        ) {
            AchievementItem(Constants.GDSC, isHighlight = true)
            AchievementItem(Constants.Solasta, isHighlight = true)
            AchievementItem(Constants.CodeChef, isHighlight = true)
            AchievementItem(Constants.Class, isHighlight = true)
            AchievementItem(Constants.school, isHighlight = true)

            AchievementItem("Accelerator Programming Camp May 2024 – July 2024", isHighlight = true)
            AchievementItem("Got selected being in top 1.4% out of 40,000 students who applied, got mentorship under the senior engineers of Apple, Google, Grab Singapore and Alphagrep Singapore.")
            AchievementItem("Learned how to build Scalable systems, deployments and gained industry-level work experience.")

            AchievementItem("Graphs camp by Codeforces Master April 2024", isHighlight = true)
            AchievementItem("Selected out of 80,000 students from India as a mentee for graphs camp by Codeforces Master and learned 17+ advanced problem solving techniques in graphs.")
            AchievementItem("Solved 3 super hard problems on the topic shortest paths and got featured in the Hall of Fame")
        }
    }
}

@Composable
fun AchievementItem(text: String, isHighlight: Boolean = false) {
    P(
        attrs = {
            style {
                fontSize(if (isHighlight) 18.px else 16.px)
                lineHeight("1.5")
                marginBottom(10.px)
                color(if (isHighlight) Color.darkblue else Color.black)
                fontWeight(if (isHighlight) FontWeight.Bold else FontWeight.Normal)
            }
        }
    ) {
        Text("• $text")
    }
}