import org.nilesh.kotlin_portfolio.utils.Res

@Composable
fun Home() {
    Box(
        modifier = HeroSectionStyle.toModifier().zIndex(0f).animation(HeroContainerKeyFrames.toAnimation(
            duration = 1.s,
            timingFunction = AnimationTimingFunction.EaseInOut,
        )),
        contentAlignment = Alignment.Center
    ) {
        Row(
            modifier = Modifier.fillMaxWidth().maxWidth(1200.px).margin(topBottom = 50.px),
            verticalAlignment = Alignment.CenterVertically
        ) {
            // Left side: Text and buttons
            Column(
                modifier = Modifier.weight(1f).paddingBlock(end = 40.px),
                horizontalAlignment = Alignment.Start
            ) {
                SpanText(
                    text = Constants.HELLO_IM,
                    modifier = HelloImStyle.toModifier()
                        .color(if (ColorMode.current.isLight) Colors.Gray else Colors.LightGray)
                        .fontWeight(FontWeight.Normal)
                        .fontSize(24.px)
                        .margin(bottom = 10.px)
                )
                SpanText(
                    text = Constants.NILESH_SHARMA,
                    modifier = UserNameStyle.toModifier()
                        .color(if (ColorMode.current.isLight) Colors.Black else Colors.White)
                        .fontWeight(FontWeight.Bold)
                        .fontSize(48.px)
                        .margin(bottom = 10.px)
                )
                SpanText(
                    text = "A Developer",
                    modifier = UsersMessageStyle.toModifier()
                        .color(if (ColorMode.current.isLight) Colors.DarkGray else Colors.LightGray)
                        .fontFamily(Res.Fonts.DM_SANS)
                        .fontSize(20.px)
                        .margin(bottom = 30.px)
                )

                val ctx = rememberPageContext()
                Row(modifier = Modifier.margin(top = 20.px).gap(20.px)) {
                    Button(
                        onClick = { ctx.router.navigateTo(Constants.RESUME_URL) },
                        colorScheme = CustomColorSchemes.BlackAndWhite,
                        size = ButtonSize.LG,
                        modifier = ButtonStyle.toModifier().width(150.px)
                    ) {
                        SpanText(
                            text = Constants.RESUME,
                            modifier = Modifier.fontFamily(Res.Fonts.Tauri)
                        )
                    }
                    Button(
                        onClick = { ctx.router.navigateTo(Constants.MAIL_TO) },
                        colorScheme = ColorSchemes.Red,
                        size = ButtonSize.LG,
                        modifier = ButtonStyle.toModifier().width(150.px)
                    ){
                        SpanText(
                            text = "Hire Me",
                            modifier = Modifier.fontFamily(Res.Fonts.Tauri)
                        )
                    }
                }
            }

            // Right side: Image
            Box(
                modifier = Modifier.weight(1f).aspectRatio(1f),
                contentAlignment = Alignment.Center
            ) {
                AppearanceAwareImage(
                    src = Res.Images.PROFILE_IMAGE,
                    modifier = Modifier.fillMaxSize(90.percent)
                        .borderRadius(20.percent)
                        .objectFit(ObjectFit.Cover)
                        .styleModifier { property("filter", "none") }
                )
            }
        }
    }
}