package org.nilesh.kotlin_portfolio.components.styles

import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import org.jetbrains.compose.web.css.keywords.auto
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.css.rgba
import org.jetbrains.compose.web.css.vh

val ProjectStyle by ComponentStyle {
    base {
        Modifier.width(100.percent)
            .minHeight(100.vh)
            .padding(top = 80.px, bottom = 80.px)
            .backgroundColor(rgba(245, 245, 245, 0.8))
    }
    Breakpoint.ZERO {
        Modifier
            .padding(top = 60.px, bottom = 60.px, left = 20.px, right = 20.px)
    }
    Breakpoint.SM {
        Modifier
            .padding(top = 70.px, bottom = 70.px, left = 30.px, right = 30.px)
    }
    Breakpoint.MD {
        Modifier
            .padding(top = 80.px, bottom = 80.px, left = 40.px, right = 40.px)
    }
    Breakpoint.LG {
        Modifier
            .padding(top = 100.px, bottom = 100.px, left = 60.px, right = 60.px)
            .maxWidth(1200.px)
            .margin(leftRight = 20.px)
    }
}