import org.nilesh.kotlin_portfolio.utils.Res

val PageContentStyle by ComponentStyle {
    base { Modifier.fillMaxSize().padding(leftRight = 2.cssRem, top = 4.cssRem) }
    Breakpoint.MD { Modifier.maxWidth(60.cssRem) }
}

@Composable
fun SVGBackgroundCircle(modifier: Modifier) {
    var rotation by remember { mutableStateOf(0f) }

    LaunchedEffect(Unit) {
        while (true) {
            delay(50)
            rotation += 0.1f
            if (rotation >= 360f) rotation = 0f
        }
    }

    AppearanceAwareImage(
        src = Res.Images.BACKGROUND_CIRCLES,
        modifier = modifier
            .rotate(rotation.deg)
            .transition(CSSTransition(property = "transform", duration = 50.ms))
    )
}

@Composable
fun PageLayout(title: String, content: @Composable ColumnScope.() -> Unit) {
    val breakpoint = rememberBreakpoint()

    val cursor = if (ColorMode.current.isDark) {
        Res.Images.CUSTOM_CURSOR_DARK
    } else {
        Res.Images.CUSTOM_CURSOR_LIGHT
    }

    LaunchedEffect(title) {
        document.title = "Nilesh Sharma - $title"
    }

    var contentVisible by remember { mutableStateOf(false) }
    var isScrolled by remember { mutableStateOf(false) }

    LaunchedEffect(Unit) {
        delay(100) // Short delay before showing content
        contentVisible = true
    }

    // Listen for scroll events
    LaunchedEffect(Unit) {
        document.addEventListener("scroll", {
            isScrolled = window.scrollY > 50
        })
    }

    Box(
        Modifier
            .fillMaxWidth()
            .minHeight(100.percent)
            .styleModifier {
                property("cursor", "url('$cursor'), auto")
            }
            .gridTemplateRows { size(1.fr); size(minContent) },
        contentAlignment = Alignment.Center
    ) {
        SVGBackgroundCircle(
            Modifier
                .align(Alignment.TopEnd)
                .pointerEvents(PointerEvents.None)
                .width(40.percent)
                .minWidth(50.vw)
                .styleModifier { property("height", "auto") }
        )

        Column(
            Modifier
                .fillMaxSize()
                .gridRow(1)
                .transition(CSSTransition(property = "opacity", duration = 500.ms))
                .opacity(if (contentVisible) 1 else 0),
            horizontalAlignment = Alignment.CenterHorizontally,
        ) {
            Column(
                PageContentStyle.toModifier(),
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                content()
            }
            BackToTopButton(
                Modifier
                    .transition(CSSTransition(property = "opacity", duration = 300.ms))
                    .transition(CSSTransition(property = "transform", duration = 300.ms))
                    .opacity(if (isScrolled) 1 else 0)
                    .styleModifier {
                        property("transform", "translateY(${if (isScrolled) "0" else "20px"})")
                    }
            )
        }
        NavHeader(
            modifier = Modifier
                .position(Position.Fixed)
                .top(0.px)
                .transition(CSSTransition(property = "transform", duration = 300.ms))
                .styleModifier {
                    property("transform", "translateY(${if (isScrolled) "0" else "-100%"})")
                }
        )
        Footer(breakpoint, Modifier.fillMaxWidth().gridRow(2))
    }
}